.col-md-6,.container{
  box-shadow: 1px 1px 10px 1px gainsboro;
  width: 100%;
  padding: 5px;
}
.one
{
  width:100%;
}
.header,.lastupdate
{
  font-family: archia;
  
}
table {
  border-collapse: collapse;
  width: 100%;
  text-align: center;
}

th, td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

tr:hover {background-color:#f5f5f5;}


.footer
{
  text-align: center;
  font-style: italic;
}
footer a{
  text-decoration: none;
}
#scrolltopBtn {
  display: none;
  position: fixed;
  bottom: 25px;
  right: 10px;
  z-index: 99;
  font-size: 15px;
  border: none;
  outline: none;
  background-color: rgb(238, 49, 49);
  color: white;
  cursor: pointer;
  padding: 10px;
  border-radius: 30px;
}